
@keyframes spinAround {
from {
    transform: rotate(0deg);
}
to {
    transform: rotate(359deg);
}
}
.app-main {
  padding-top: 50px;
  margin-left: 180px;
  transform: translate3d(0, 0, 0);
}
@media screen and (max-width: 768px) {
.app-main {
      margin-left: 0;
}
}
.app-content {
  padding: 20px;
}



